import styled from 'styled-components';

import Touch from './assets/Touch.jpg';

//Home.js Styles
export const StyledHome = styled.div`
@import url('https://fonts.googleapis.com/css?family=KoHo:400,500&display=swap');

.home-contents{
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #336075;
  color: #fff;
  height: 315px;
  position: relative;
  z-index: 1;

  @media(max-width: 499px){
    height: 220px;
  }

  ::after{
    background: inherit;
    bottom: 0;
    content: '';
    display: block;
    height: 690px;
    left: 0;
    position: absolute;
    right: 0;
    border-bottom: 15px solid #c6e8f2;
    transform: skewY(-8.5deg);
    transform-origin: 104%;
    z-index: -1;
  }

  &::before{
    background-image: url(${Touch});
    top: 216px;
    background-color: #080808;
    background-position-x: right;
    background-position-y: bottom;
    background-repeat-x: no-repeat;
    background-repeat-y: no-repeat;
    content: '';
    display: block;
    height: 710px;
    left: 0;
    position: absolute;
    right: 0;
    transform: skewY(0deg);
    transform-origin: 100%;
    z-index: -1;
    @media(max-width: 450px){
      height: 500px;
      width: 500px;
      padding-top: 50px;
      padding-left: 160px;
      }
    }
  
  .welcome-container{
    padding-left: 20px;
    .welcome-text{
      font-family: 'KoHo', sans-serif;
      font-size: 3rem;
      color: #ffffff;
      font-weight: 700;
      margin-block-start: 0em;
      margin-block-end: 0em;
      @media(max-width: 750px){
        font-size: 2.5rem;
      }
    }
    .animated-text{
      font-family: 'KoHo', sans-serif;
      font-size: 2rem;
      color: #ffffff;
      margin-block-start: 0em;
      margin-block-end: 0em;
      @media(max-width: 750px){
        font-size: 1.3rem;
      }
    }
  }
  .logo{
    height: 48px;
    margin-right: 40px;
    margin-top: 20px;
  }

  .modal-container{
    position: fixed;
    display: flex;
    border-radius: 50px;
    border-radius: 0px;
    background: #c8eaf1;
    box-shadow:  -12px 12px 0px #a4c1c9, 
             12px -12px 0px #e8ffff;
    flex-direction: column;
    top: 45%;
    right: 30px;
    height: 240px;
    justify-content: space-between;
    @media(max-width: 499px){
      box-shadow: none;
      background: none;
      height: 230px;
    }
  }

  .show-modal-button{
  font-family: 'KoHo', sans-serif;
  color: #000000;
  font-size: 1em;
  border: none;
  cursor: pointer;
  text-align: center;
  font-weight: 700;
  user-select: none;
  background: #c8eaf1;
  height: 70px;
  width: 70px;
  

  @media(max-width:499px){
    height: 70px;
    width: 70px;
    border: 3px solid #336075;
    font-size: .9em;
    border-radius: 50%;
    font-weight: 500;
  }
}
.show-modal-button:hover,
.show-modal-button:focus {
  border: none;
  font-size: 1.1em;
  outline: none;
}

  .show-modal-button-ed{
    font-family: 'KoHo', sans-serif;
    background-color: #336075;
    color: #ffffff;
    font-size: 1em;
    border: none;
    cursor: pointer;
    text-align: center;
    font-weight: 700;
    user-select: none;
    border: 3px solid #c6e8f2;
    height: 70px;
    width: 70px;
    border-radius: 50%;

    @media(max-width:499px){
      height: 50px;
      width: 50px;
      font-size: .9em;
      font-weight: 500;
    }
    }
    .show-modal-button-ed:hover,
    .show-modal-button-ed:focus {
      border: none;
      outline: none;
    }

    .show-modal-button-con{
      font-family: 'KoHo', sans-serif;
      background-color: #336075;
      color: #ffffff;
      font-size: 1em;
      border: none;
      cursor: pointer;
      text-align: center;
      border: 3px solid #c6e8f2;
      font-weight: 700;
      user-select: none;
      height: 70px;
      width: 70px;
      border-radius: 50%;
  
      @media(max-width:499px){
        height: 50px;
        width: 50px;
        font-size: .9em;
        font-weight: 500;
      }
      }
      .show-modal-button-con:hover,
      .show-modal-button-con:focus {
        border: none;
        outline: none;
      }

  .modal {
    width: 325px;
    height: 300px;
    color: #fff;
    border-radius: 8px;
    background: black;
    padding: 30px;
    border-radius: 50px;
    border-radius: 0px;
    background: linear-gradient(225deg, black, black);
    box-shadow:  -8px 8px 0px #c6e8f2, 
             8px -8px 0px #c6e8f2;
    font-family: 'KoHo', sans-serif;
    position: fixed;
    z-index: 90;
    border: 2px solid white;
    top: calc(50% - 110px);
    left: calc(50% - 195px);
    display: flex;
    flex-direction: column;

    .modal-title{
      margin-block-start: 0em;
      margin-block-end: 0em;
    }

    .modal-content{
      margin-block-start: .2em;
      
      margin-block-end: .2em;
    }
  }
  
  .modal-close-button {
    padding: 6px;
    background-color: black;
    color: #ffffff;
    font-weight: 700;
    font-size: 1.2em;
    border: none;
    position: absolute;
    top: 10px;
    right: 15px;
    width: 30px;
    cursor: pointer;
    transition: background-color 0.1s linear;
  }
  
  .modal-close-button:hover,
  .modal-close-button:focus {
    color: #c6e8f2;
  }
}


`



// // Projects.js Styles
export const StyledProjects = styled.div`
  @import url('https://fonts.googleapis.com/css?family=Red+Hat+Display&display=swap');
  font-family: 'Red Hat Display', sans-serif;
  margin: 0 auto;
  
  width: 1250px;
  

  @media(max-width: 600px){
    width: 100%;
    margin-top: 0px;
    
  }

  @media(max-width: 499px){
    padding-left: 25px;
  }
  
  .react-multi-carousel-list{
    height: 550px;
    @media(max-width: 500px){
      height: 500px;
    }
  }

  .projects-container{
    background: black !important;
    @media(max-width: 499px){
      margin-top: -80px;
    }
  }

  

  .custom-button-group{
    position: absolute;
    bottom: 0;
    left: 45%;
    display: flex;
    cursor: pointer;
    width: 350px;
    color: white;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    ::selection{
      background-color: red;
    }

    @media(max-width: 600px){
      left: 30%;
    }
  }
  .arrow{
    margin-right: 10px;
    font-size: 1.2rem;
    text-decoration: underline;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .title-br{
  width: 360px;
  color: white;
  }

  .project-image{
    position: fixed;
    width: 360px;
    height: 360px;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
    top: 0;
    left: 0;
  }

  ul {
    list-style: none;
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-inline-start: 0px;
  }

  .a-link {
    display: none;
  }

  .flippy-container {
    border-radius: 8px;
    height: 360px !important;
    overflow: hidden;
    background-image: #001920;
    box-shadow: 0px 0px 7px #001920;
  }

  .design-desc {
    text-align: center;
  }

  .framework-desc {
    text-align: center;
  }

  p {
    margin-block-start: 0px;
    margin-block-end: 0px;

    padding: 2%;
  }

  .card {
    position: relative;

    @media(max-width: 1100px){
      margin: 0 auto;
    }

    .click-here-text{
      position: absolute;
      bottom: 54px;
      font-size: 1.2em;
      cursor: default;

      &:hover{
        font-weight: 500;
      }

      @media(max-width: 600px){
        font-size: 1em;
      }
    }

    .click-here{
      position: absolute;
      bottom: 10px;
      right: 14px;
      text-transform: uppercase;
      font-size: 6em;
      color: #c6e8f2;

      @media(max-width: 600px){
        font-size: 3em;
        bottom: 40px;
      }

      &:hover{
        color: #A8C5FF;
        font-weight: 100;
      }
    }

    h3{
      font-weight: 700;
      margin-block-start: 0em;
      margin-block-end: 0em;
    }
  }

  .card-links {
    position: fixed;
    bottom: -31px;
    left: 31px;
    color: black;
    width: 360px;
    display: flex;
    justify-content: space-between;
    @media(max-width: 499px){
      left: 0px;
    }
    

    .link {
      text-decoration: none;
      padding: 2%;
      color: white;
      font-weight: 500;
      

      &:hover{
        color: #001920;
        font-weight: 500;
      }
    }

  }

  h3 {
    text-align: center;
    margin-block-start: .5em;
    margin-block-end: .5em;
  }

  h4 {
    text-align: center;
    font-weight: 500;
    margin-block-start: .1em;
    margin-block-end: .1em;
    
  }

  .proj-desc{
    margin-top: 10px;
  }

  .react-multi-carousel-item--active{
    width: 390px !important;
    padding-left: 30px;

    @media(max-width: 499px){
      padding-left: 0px;
    }
  }

`;

//Footer.js Styles
export const StyledFooter = styled.div`
@import url('https://fonts.googleapis.com/css?family=KoHo:400,500&display=swap');
.footer-container{
  width: 100%;
  height: 70px;
  position: absolute;
  bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  z-index: 1;
  

  .footer-contents{
    display: flex;
    padding-left: 10px;
    color: white;
    align-items: center;
    font-family: 'KoHo', sans-serif;
  }

  .footer-icon-container{
    width: 300px;
    display: flex;
    justify-content: space-between;
    @media(max-width: 500px){
      width: 100%;
      padding-left: 0px;
      justify-content: space-evenly;
    }
  }


  .png{
    background: radial-gradient(#336075 75%, #0a0a0a 25%);
    border-radius: 50%;
    border: 3px solid #336075;
    height: 55px;
    width: 55px;

    @media(max-width:499px){
      height: 50px;
      width: 50px;
    }
    

    &:hover{
      background: #0a0a0a;
      border: 3px solid #000000;
    }
  }
}

`

